* {
    box-sizing: border-box;
}
button {
    outline: none;
    border: 0;
    cursor: pointer;
}
.navbar {
    background-color: beige;
    display: flex;
    align-items: center;
    padding: 20px 10px;
}
.navbar .icon {
    color: green;
    margin-right: 10px;
}
.navbar .title {
    font-size: 1rem;
    font-weight: bold;
}
.navbar .count {
    width: 25px;
    height: 25px;
    line-height: 25px;
    font-size: 1.2rem;
    text-align: center;
    color: white;
    background-color: green;
    border-radius: 50%;
    margin-left: 10px;
}

.add-form {
    
}

.habit {
    font-size: 2.5rem;
    padding: 0.5em;
    list-style: none;
}
.habit-count {
    display: inline-block;
    text-align: center;
    font-size: 2rem;
    width: 2.5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    margin-left: 0.2em;
    margin-right: 0.5em;
    background-color: darkseagreen;
    color: white;
    border-radius: 50%;
}
.habit-button {
    font-size: 2.5rem;
    margin: 0 0.1rem;
    background-color: transparent;
}
.habit-button:hover {
    opacity: 0.8;
}
.habit-increase,
.habit-decrease {
    color: goldenrod;
}
.habit-delete {
    color: darkred;
}
